<h1 id="webpack中的一些概念" tabindex="-1"><a class="header-anchor" href="#webpack中的一些概念" aria-hidden="true">#</a> Webpack中的一些概念</h1>
<p>entry(入口)，output(输出)，loaders, plugins</p>
<h2 id="entry" tabindex="-1"><a class="header-anchor" href="#entry" aria-hidden="true">#</a> entry</h2>
<p>entry point(入口起点)</p>
<h2 id="output" tabindex="-1"><a class="header-anchor" href="#output" aria-hidden="true">#</a> output</h2>
<p><code>output</code>指定webpack在哪里输出它所创建的<code>bundles</code>，默认的输出路径<code>./dist</code>。</p>
<h2 id="loader" tabindex="-1"><a class="header-anchor" href="#loader" aria-hidden="true">#</a> loader</h2>
<p>处理非Javascript文件，转换为<code>webpack</code>能够理解的有效<code>模块</code></p>
<h2 id="plugins" tabindex="-1"><a class="header-anchor" href="#plugins" aria-hidden="true">#</a> plugins</h2>
